* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

body {
  /* background: linear-gradient(
    90deg,
    rgba(48, 16, 255, 1) 0%,
    rgba(100, 115, 255, 1) 100%
  ); */
  background-image: radial-gradient(circle at 50% -20.71%, #a3a7ff 0, #7a8ffc 25%, #3c78f2 50%, #0063e8 75%, #0051de 100%);
}


.App{
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 600px;
  min-height: 600px;
  background: #161a2b;
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
  left: -100px;
  border: black 2px solid;
}

.form{
  /* width: 80%;
  margin-top: 20px;
  margin-left: 80px; */

  margin-bottom: 32px;
  flex-direction: row;
}

.form span {
  /* font-size: 25px;
  margin-right: 20px; */

  margin: 32px 0;
  color: #fff;
  font-size: 40px;
  
}

h6{
  margin-left: -100px;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  outline: none;
}
.form input{
  /* margin-right: 30px; */

  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #5d0cff;
  outline: none;
  width: 320px;
  background: transparent;
  color: #fff;
}
.form-input::placeholder {
  color: #e2e2e2;
}


.form button{
  /* border: none;
  padding: 15px;
  font-size: 20px;
  background-color: black;
  color: white; */
  padding: 16px 27px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  color: #fff;
  text-transform: capitalize;
}

.validacion{
  width: 80%;
  color: red;
  margin-left: 80px;
}

.listas{
  width: 80%;
  margin-left: 80px;
  
}

.lista{
  font-size: 20px;
  margin: 20px 0 15px 0;
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: #fff;
  background: linear-gradient(
    90deg,
    rgba(20, 159, 255, 1) 0%,
    rgba(250, 0, 135, 1) 100%
  );
  
  padding: 16px;
  border-radius: 5px;
  width: 112%;
  margin-left: -50px;
}

